.hero_wrapper {
  background-color: #0beef9;
  background-image: linear-gradient(215deg, #48a9fe 0, #0beef9 64%);
  clip-path: ellipse(100% 66% at 22% 27%);
}
.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 0 160px 0;
}
.hero .img_wrap {
  margin-right: 120px;
  width: 100%;
  max-width: 500px;
  border-radius: 20px;
  overflow: hidden;
}
.hero__img {
  width: 100%;
}

.hero .heading-primary {
  font-size: 6rem;
  margin-bottom: 30px;
}
.hero .paragraphy {
  font-size: 3rem;
}

@media (max-width: 56.25em) {
  .hero {
    flex-direction: column;
    padding: 40px 0 190px 0;
  }

  .hero .img_wrap {
    margin: 0 0 20px 0;
  }
  .hero .heading-primary,
  .hero .paragraphy {
    text-align: center;
  }
}

@media (max-width: 56.25em) {
  .hero_wrapper {
    clip-path: none;
  }
}
@media (max-width: 36.5em) {
  .hero {
    padding: 30px 0 70px 0;
  }
}
